body {
  background-color: rgba(53, 53, 53, 1);
  font-family: 'Eras ITC', sans-serif;
  overflow-x: hidden;
  overflow-y: auto;
}

.bg-images {
  display: flex;
  flex-direction: column;
  height: 100vh;
  width: 100%; /* Ajustado para que ocupe todo el ancho sin exceder */
  margin: auto;
  overflow: hidden; /* Evita desbordamiento no deseado */
  z-index: -1;
}

#index {
  padding-top: 80vh;
  padding-bottom: 10vh;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  justify-items: center;
  align-items: end;
  margin: auto; /* Centraliza el contenido */
  width: 90%; /* Ajuste dinámico para móviles */
  height: auto; /* Evita una altura fija que genere problemas */
}

#IndexColorLogo {
  margin-bottom: 10vh;
  width: 80%; /* Asegura que se ajuste en pantallas pequeñas */
  height: auto; /* Evita valores fijos */
}

#jrText {
  background-color: rgba(250, 250, 250, 0.548);
  padding: 3%;
  padding-inline: 4%;
  border-radius: 20px;
  border-style: dashed;
  border-color: #000000d5;
  border-width: 3px;
}

.PorQueJr {
  row-gap: 5rem; /* Usar rem para evitar problemas de escala en móviles */
  grid-template-rows: auto auto;
  width: 100%; /* Ajuste dinámico */
  height: auto;
}

.JrTeam {
  grid-column: span 2;
  width: 80%; /* Redimensiona el equipo en pantallas pequeñas */
  max-width: 32vw; /* Máximo para pantallas grandes */
}

.no-scroll {
  overflow: hidden; /* Esto evitará que se haga scroll en esta sección específica */
}

/* Media Queries para dispositivos pequeños */
@media (max-width: 450px) {
  .bg-images {
      width: 100%; /* Asegura que no exceda el ancho del viewport */
      filter: blur(3px);
      height: auto;
  }

  #index {
      padding-top: 50vh; /* Reduce el espacio superior */
      padding-bottom: 5vh; /* Reduce el espacio inferior */
      grid-template-columns: 1fr; /* Una sola columna en móviles */
  }

  #IndexColorLogo {
      width: 60%; /* Más pequeño en móviles */
      margin: auto;
  }

  .JrTeam {
      width: 100%; /* Ocupa todo el ancho disponible */
  }

  .PorQueJr {
      row-gap: 2rem; /* Espaciado más pequeño */
  }

  #jrText {
      padding: 5%;
