#bg-map {
    margin: 2rem auto;
    background-color: rgba(0, 0, 0, 0.5);
    padding: 20px;
    border-radius: 20px;
    width: 90%;
    max-width: 800px; /* Limita el ancho en pantallas grandes */
    text-align: center;
}

#index {
    padding-top: 50vh;
    padding-bottom: 30vh;
}

#Us {
    background-color: rgba(229, 229, 229, 0.40);
    box-shadow: 0px 8px 8px 10px rgba(0, 0, 0, 0.25);
    display: flex;
    flex-wrap: wrap; /* Permite que los elementos se ajusten */
    justify-content: center;
    align-items: center;
    padding: 5%;
    width: 90%;
    max-width: 1200px; /* Limita el ancho máximo */
    height: auto; /* Altura dinámica */
    margin: 5rem auto;
}

.JrTeamdiv {
    position: relative; /* Evita problemas con `absolute` */
    background-color: rgba(250, 250, 250, 0.548);
    padding: 3%;
    border-radius: 20px;
    border-style: dashed;
    border-color: #000000d5;
    border-width: 3px;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 2rem auto;
}

/* Media Queries para dispositivos pequeños */
@media (max-width: 450px) {
    #index {
        display: flex;
        flex-direction: column;
        align-items: center;
        padding: 10vh 5%;
        font-size: 1.5rem;
    }

    .JrTeam {
        width: 100%;
        margin: 5vh auto;
    }

    .JrTeamdiv {
        width: 90%;
        padding: 2%;
        border-width: 2px;
        margin-top: 5vh;
    }

    #IndexColorLogo {
        width: 80%;
        height: auto;
        margin: 2rem auto;
    }

    #jrText {
        width: 90%;
        padding: 2%;
        background-color: rgba(255, 255, 255, 0.9);
        margin: 5vh auto;
    }

    .PorQueJRheader {
        font-size: 2rem;
    }

    #bg-map {
        margin-top: 5vh;
        width: 90%;
        max-width: 600px;
        padding: 1rem;
    }

    #bg-map iframe {
        width: 100%;
        height: 50vh; /* Ajuste dinámico */
    }

    #mapTxt {
        color: #fff;
        font-size: 1.5rem;
        text-align: center;
    }

    #Us {
        flex-direction: column;
        align-items: center;
        width: 100%;
        padding: 5%;
        background-color: transparent;
        box-shadow: none;
    }
}

@media (min-width: 420px) and (max-width: 450px) {
    #index {
        margin-top: 80vh;
    }
}
