#sub-nav-barL {
    margin: auto;
    align-items: center;
    justify-content: space-evenly;
    color: rgba(255, 255, 255, 1);
    font-size: 24px;
    font-weight: 450;
    list-style: none;
    padding: 0;
    position: fixed;
    top: 18.5%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 1;
    width: 100vw;
    height: 8vh;
    border: 0.5px solid #c9c9c9;
    border-radius: 0px;
    display: flex;
    background: rgba(24, 24, 24, 1);
}

#menu {
    padding-top: 1510vh;
}

.menuImg {
    margin: auto;
    display: flex;
    align-items: center;
    flex-direction: row;
    position: absolute;
    scroll-margin-top: 100%;

}

